*{
  padding:0;
  margin:0;
  box-sizing:border-box;
}
.content{
  height: 100vh;
}
.navbar{
  background-color: #d138bf !important;
}
.register-page{
  display:flex;
  justify-content: center;
  align-items: center;
  height: 90vh;
}

/* transection page */
.filters{
  display:flex;
  align-items:center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px rgba(128, 128, 128, 0.982);
}

.anticon svg{
  font-size: 20px;
  cursor:pointer;
}

.active-icon{
  color:black;
}
.inactive-icon{
  color:green
}
.switch-icons{
  border:1px solid rgba(0, 0, 0, 0.648);
  border-radius: 5px;
  padding: 10px 15px;
}